import './App.css';
import {NavLink, Route, Routes} from "react-router-dom";
import Main from "./components/mainPage/Main";
import LoginPage from "./components/loginPage/LoginPage";
import AdminPage from "./components/adminPage/AdminPage";
import { useDispatch, useSelector} from "react-redux";
import PrivateRoute from "./components/adminPage/PrivateRouteForAdmin";
import PrivateRouteLogin from "./components/loginPage/PrivateRouteForLogin";
import React from 'react';



function App() {
    const dispatch = useDispatch();
    const isAuth = useSelector((state) => state.loginReducer.isAuth)

  return (
    <div className="App">
        <header className="header">
            <NavLink className="link" to="/">HOME</NavLink>
            {isAuth? <a  className="link"  onClick={() => dispatch({type: 'setIsAuth', payload: false})}>LOGOUT</a> :  <NavLink className="link" to="/login">LOGIN</NavLink>}
            <NavLink className="link" to="/admin">ADMIN</NavLink>

        </header>
            <Routes>
                <Route path="/" element={<Main/>}></Route>
                <Route path="/login" element={<PrivateRouteLogin  redirectPath={"/admin"} children={<LoginPage />}/>}></Route>
                <Route path='/admin' element={<PrivateRoute  redirectPath={"/login"} children={<AdminPage/>}/>}></Route>
            </Routes>
    </div>
  );
}

export default App;
