import React, {useEffect, useState} from 'react';
import Ms from "./mainStyle.module.css";
import {useDispatch, useSelector} from "react-redux";
import {addInfo, getInfo} from "../../firebase/firebaseAction";
import ButtonSignUp from "./button/Button"
import {actionCreate} from "../../redux/reducers/resumeInfoReducer";


const Main = () => {
    //отримую дані з стейта

    const data = useSelector((state) => state.resumeInfoReducer.inforesume)
    const isAuth = useSelector((state) => state.loginReducer.isAuth)

        //хук з реакт-редакс
    const dispatch = useDispatch();
    //це локалльний стейт де я зберігаю дані з "БД" і потім відправлю ії в стор
    const [store, setStore] = useState();

    useEffect(() => {
        // addInfo(
        //     {name: "Марк",
        //             surname: "Горбайчук",
        //             dateofbirth: "01.06.2002",
        //             placeOfResidence: "city.Lviv",
        //             contacts: [
        //             {main: "Номер телефону: ", info: "+380971195151"},
        //             {main: "Електронна пошта: ", info: "brainocswell@gmail.com"},
        //         ],
        //             hardAndSoftSkills: [
        //             {data: "вміння працювати в команді, критичне мислення, лідерство, креативність," +
        //                     " вміння дотримуватися дедлайни, відповідальність та дисциплінованість"},
        //             {data: "TypeScript, JS, HTML5, CSS3, bootstrap, React, Redux ect."}
        //         ],
        //             experiance: "Немає комерційного досвіду",
        //             education: [
        //             {data: "унівеситет: вища незакінчена освіта. Спеціальність:Компютерні науки"},
        //             {data: "Lgoos IT Academy"},
        //         ]}, 'test')
        getInfo(setStore, "test");
    }, []);

    //перевіряю якщо в локал стейт є дані відправляю іх в сторе через діспач
    if(store)  dispatch(actionCreate(store))



    return (
        <div className={Ms.mainBlock}>

            <div className={Ms.Resume}>
                <h3>Резюме</h3>
            </div>

            <div className={Ms.nameAndSurname}>
                <h2 style={{fontSize: '22px'}}>{data?.name}</h2>
                <h2 style={{fontSize: '22px'}}>{data?.surname}</h2>
            </div>

            <div className={Ms.birthday}>
                <h4>Дата народження:</h4>
                <p >{data?.dateofbirth}</p>
            </div>

            <div className={Ms.education}>
                <h4 style={{textAlign: 'center'}}>Освіта та комерційний досвід</h4>
                {data?.education.map(e => <p key={e.index}>{e.data}</p>)}
                <p >Досвід в ІТ: {store?.experiance}</p>
            </div>


            <div className={Ms.skills}>
                <h4 style={{textAlign: 'center'}}>Навички</h4>
                {data?.hardAndSoftSkills.map(e => <p key={e.index} className={Ms.skills}>{e.data}</p>)}
            </div>

            <div className={Ms.contacts}>
                <h4 style={{textAlign: 'center'}}>Контакти</h4>
                {data?.contacts.map(e => <p key={e.index}>{e.main} <span>{e.data}</span></p>)}
            </div>


            {isAuth?  <ButtonSignUp text={"Change data"}  path={"/admin"} /> :  <ButtonSignUp text={"Sign in"}  path={"/login"} />}


        </div>
    );
};

export default Main;